.event-addattendee {
    .MuiPaper-root {
        background-color: initial;
        padding: 32px;
        width: 100%;
    }
}

.add-attendee {
    background-color: white;
    border-radius: 20px;
    max-width: 343px;
    width: 343px;
    z-index: 10;
}

.add-attendee-header {
    border-bottom: 1px solid #e4e4e4;
    font-size: 1.3125rem;
    font-weight: 400;
    padding: 20px 16px;
}

.add-attendee-details {
    padding: 16px;
}

.add-attendee-details-toggle {
    background-color: #f1f2ff;
    border-radius: 20px;
    display: inline-block;
}

.add-attendee-details-toggle-pill {
    color: #38539a;
    cursor: pointer;
    display: inline-block;
    font-size: .875rem;
    font-weight: 700;
    padding: 9px 17px;
    text-align: center;
    width: 92px;

    &.active {
        background-color: #38539a;
        border-radius: 24px;
        color: #f1f2ff;
        font-weight: 700;
    }
}

.add-attendee-details-form {
    padding-top: 32px;

    .import{
        color: #38539a;
        cursor: pointer;
        font-size: .875rem;
        font-weight: 700;
        text-decoration: underline;
    }
}

.add-attendee-details-form-guest {

    input:not(.PhoneInputInput){
        border: 2px solid #2c2c2c;
        border-radius: 10px;
        color: #707070;
        font-size: .875rem;
        height:36px;
        margin-bottom: 16px;
        padding: 7px 12px;
        width: 100%;
        
    }

    .PhoneInput{
        border: 2px solid #2c2c2c;
        border-radius: 10px;
        color:#707070;
        font-size: .875rem;
        height: 36px;
        margin-bottom: 16px;
        padding: 7px 12px;
        width: 100%;
    }

    select{
        border: 2px solid #2c2c2c;
        border-radius: 10px;
        color: #707070;
        font-size: .875rem;
        height: 36px;
        margin-bottom: 16px;
        padding: 7px 12px;
        width: 100%;
    }
}

input {
    color: inherit;
    font-family: inherit;
    font-size: 100%;
    font-weight: inherit;
    line-height: inherit;
    margin: 0;
    padding: 0;

}

select{
    text-transform: none;
    color: inherit;
    font-size: 100%;
    font-weight: inherit;
    font-weight: inherit;
    margin: 0;
    padding: 0;
}


.add-attendee-buttons {
    display: flex;
    padding: 33px 16px;
}

.add-button {
    display: inline-block;
    background-color: black;
    border: 1px solid black;
    border-radius: 100px;
    color: #fff;
    cursor: pointer;
    font-size: .875rem;
    font-weight: 400;
    height: 100%;
    line-height: 30px;
    position: relative;
    text-align: center;
    width: 100%;
    margin: 6px;

    &.gray {
        background-color: #e4e4e4;
        border: 1px solid #e4e4e4;
        color: #707070;
        font-weight: 700;
        pointer-events: none;
    }
}
